import React, { useState, useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import TitleData from "../data/TitleData";
import EducationData from "../data/EducationData";

import LightBox from "./LightBox";
import Trophy from "./Trophy";
import UniversityProgram from "./UniversityProgram";
import texas_logo from "../images/Other/texas_logo.png";
import mit_logo from "../images/Other/mit_logo.png";
import unipr_logo from "../images/Other/unipr_logo.png";
import pgp_aiml from "../images/Certifications/pgp_aiml.png";
import pgp_cc from "../images/Certifications/pgp_cc.png";
import pgp_bd from "../images/Certifications/pgp_bd.png";
import mit from "../images/Certifications/mit.png";
import unipr from "../images/Certifications/unipr.png";
import grade_aiml from "../images/Certifications/grade_aiml.png";
import grand_hackathon_certificate from "../images/Certifications/grand_hackathon_certificate.png";
import hackathon_certification from "../images/Certifications/hackathon_certification.png";
import leaderboard from "../images/Certifications/leaderboard.png";

import AIMLProgram from "./AIMLProgram";
import CCProgram from "./CCProgram";
import MBDProgram from "./MBDProgram";
import MITProgram from "./MITProgram";
import MEProgram from "./MEProgram";

export default function Education() {
  const language = useContext(LanguageContext)[0];

  const [display1, setDisplay1] = useState("none");
  const [display2, setDisplay2] = useState("none");
  const [display3, setDisplay3] = useState("none");
  const [display4, setDisplay4] = useState("none");
  const [display5, setDisplay5] = useState("none");
  const [btn1, setBtn1] = useState("");
  const [btn2, setBtn2] = useState("");
  const [btn3, setBtn3] = useState("");
  const [btn4, setBtn4] = useState("");
  const [btn5, setBtn5] = useState("");

  function handleClickTab1() {
    if (display1 === "none") {
      setDisplay1("");
      setBtn1("btn-education-selected");
    } else {
      setDisplay1("none");
      setBtn1("");
    }
    setDisplay2("none");
    setBtn2("");
    setDisplay3("none");
    setBtn3("");
    setDisplay4("none");
    setBtn4("");
    setDisplay5("none");
    setBtn5("");
  }

  function handleClickTab2() {
    setDisplay1("none");
    setBtn1("");
    if (display2 === "none") {
      setDisplay2("");
      setBtn2("btn-education-selected");
    } else {
      setDisplay2("none");
      setBtn2("");
    }
    setDisplay3("none");
    setBtn3("");
    setDisplay4("none");
    setBtn4("");
    setDisplay5("none");
    setBtn5("");
  }

  function handleClickTab3() {
    setDisplay1("none");
    setBtn1("");
    setDisplay2("none");
    setBtn2("");
    if (display3 === "none") {
      setDisplay3("");
      setBtn3("btn-education-selected");
    } else {
      setDisplay3("none");
      setBtn3("");
    }
    setDisplay4("none");
    setBtn4("");
    setDisplay5("none");
    setBtn5("");
  }

  function handleClickTab4() {
    setDisplay1("none");
    setBtn1("");
    setDisplay2("none");
    setBtn2("");
    setDisplay3("none");
    setBtn3("");
    if (display4 === "none") {
      setDisplay4("");
      setBtn4("btn-education-selected");
    } else {
      setDisplay4("none");
      setBtn4("");
    }
    setDisplay5("none");
    setBtn5("");
  }

  function handleClickTab5() {
    setDisplay1("none");
    setBtn1("");
    setDisplay2("none");
    setBtn2("");
    setDisplay3("none");
    setBtn3("");
    setDisplay4("none");
    setBtn4("");
    if (display5 === "none") {
      setDisplay5("");
      setBtn5("btn-education-selected");
    } else {
      setDisplay5("none");
      setBtn5("");
    }
  }

  return (
    <div className="education-container" id="education">
      <div className="row">
        <div className="title-section title-section-education">
          {TitleData[language][3].toUpperCase()}
        </div>
      </div>

      <div className="row">
      <div className="col-lg-1"></div>
        <div className="col-lg-2 col-md-6 col-sm-6">
          <div className="row education-space"></div>
          <div className="row justify-content-center">
            <img src={texas_logo} alt="uni-img" className="uni-img" />
          </div>
          <div className="row justify-content-center">
            <LightBox
              certificate={pgp_aiml}
              reference={"pgp_aiml"}
              color={"#1b262c"}
            />
            <div className="trophy">
              &emsp;
              <Trophy
                reference="grade_aiml"
                certificate={grade_aiml}
                description={EducationData.trophy[language][0]}
              />
              &ensp;
              <Trophy
                reference="hackathon_certification"
                certificate={hackathon_certification}
                description={EducationData.trophy[language][1]}
              />
              &ensp;
              <Trophy
                reference="grand_hackathon_certificate"
                certificate={grand_hackathon_certificate}
                description={EducationData.trophy[language][2]}
              />
              &ensp;
              <Trophy
                reference="leaderboard"
                certificate={leaderboard}
                description={EducationData.trophy[language][3]}
              />
            </div>
          </div>
          <div className="row justify-content-center">
            <div className="uni-course">
              {EducationData.course[language][0]}
            </div>
          </div>

          <div className="row justify-content-center">
            <div className="tooltips tooltips-education">
              <i className="fas fa-info-circle"></i>
              <span className="tooltiptext">
                {EducationData.date[language][0]}
              </span>
            </div>
            <div className="uni-type">{EducationData.type[language][0]}</div>
          </div>
          <div className="row justify-content-center"></div>
          <div className="row justify-content-center btn-education-container">
            <a
              href="#tab1"
              className={
                "btn shadow-none btn-outline-light btn-sm btn-education " + btn1
              }
              onClick={handleClickTab1}
            >
              {language === "UK" ? "Program" : "Programma"}
            </a>
          </div>
        </div>

        <div className="col-lg-2 col-md-6 col-sm-6">
        <div className="row education-space"></div>
          <div className="row justify-content-center">
            <img src={texas_logo} alt="uni-img" className="uni-img" />
          </div>
          <div className="row justify-content-center">
            <LightBox
              certificate={pgp_cc}
              reference={"pgp_cc"}
              color={"#1b262c"}
            />
          </div>
          <div className="row justify-content-center">
            <div className="uni-course">
              {EducationData.course[language][1]}
            </div>
          </div>

          <div className="row justify-content-center">
            <div className="tooltips tooltips-education">
              <i className="fas fa-info-circle"></i>
              <span className="tooltiptext">
                {EducationData.date[language][1]}
              </span>
            </div>
            <div className="uni-type">{EducationData.type[language][1]}</div>
          </div>
          <div className="justify-content-center btn-education-container">
            <a
              href="#tab2"
              className={
                "btn shadow-none btn-outline-light btn-sm btn-education " + btn2
              }
              onClick={handleClickTab2}
            >
              {language === "UK" ? "Program" : "Programma"}
            </a>
          </div>
        </div>

        <div className="col-lg-2 col-md-6 col-sm-6">
        <div className="row education-space"></div>
          <div className="row justify-content-center">
            <img src={texas_logo} alt="uni-img" className="uni-img" />
          </div>
          <div className="row justify-content-center">
            <LightBox
              certificate={pgp_bd}
              reference={"pgp_bd"}
              color={"#1b262c"}
            />
          </div>
          <div className="row justify-content-center">
            <div className="uni-course">
              {EducationData.course[language][2]}
            </div>
          </div>

          <div className="row justify-content-center">
            <div className="tooltips tooltips-education">
              <i className="fas fa-info-circle"></i>
              <span className="tooltiptext">
                {EducationData.date[language][2]}
              </span>
            </div>
            <div className="uni-type">{EducationData.type[language][2]}</div>
          </div>
          <div className="justify-content-center btn-education-container">
            <a
              href="#tab3"
              className={
                "btn shadow-none btn-outline-light btn-sm btn-education " + btn3
              }
              onClick={handleClickTab3}
            >
              {language === "UK" ? "Program" : "Programma"}
            </a>
          </div>
        </div>

        <div className="col-lg-2 col-md-6 col-sm-6">
        <div className="row education-space"></div>
          <div className="row justify-content-center">
            <img src={mit_logo} alt="uni-img" className="uni-img uni-img-mit" />
          </div>
          <div className="row justify-content-center">
            <LightBox
              certificate={mit}
              reference={"mit"}
              color={"#1b262c"}
            />
          </div>
          <div className="row justify-content-center">
            <div className="uni-course">
              {EducationData.course[language][3]}
            </div>
          </div>

          <div className="row justify-content-center">
            <div className="tooltips tooltips-education">
              <i className="fas fa-info-circle"></i>
              <span className="tooltiptext">
                {EducationData.date[language][3]}
              </span>
            </div>
            <div className="uni-type">{EducationData.type[language][3]}</div>
          </div>
          <div className="justify-content-center btn-education-container">
            <a
              href="#tab4"
              className={
                "btn shadow-none btn-outline-light btn-sm btn-education " + btn4
              }
              onClick={handleClickTab4}
            >
              {language === "UK" ? "Program" : "Programma"}
            </a>
          </div>
        </div>


      <div className="col-lg-2 col-md-6 col-sm-6">
      <div className="row education-space"></div>
        <div className="row justify-content-center">
          <img src={unipr_logo} alt="uni-img" className="uni-img" />
        </div>
        <div className="row justify-content-center">
          <LightBox
            certificate={unipr}
            reference={"unipr"}
            color={"#1b262c"}
          />
        </div>
        <div className="row justify-content-center">
          <div className="uni-course">
            {EducationData.course[language][4]}
          </div>
        </div>

        <div className="row justify-content-center">
          <div className="tooltips tooltips-education">
            <i className="fas fa-info-circle"></i>
            <span className="tooltiptext">
              {EducationData.date[language][4]}
            </span>
          </div>
          <div className="uni-type">{EducationData.type[language][4]}</div>
        </div>
        <div className="justify-content-center btn-education-container">
          <a
            href="#tab5"
            className={
              "btn shadow-none btn-outline-light btn-sm btn-education " + btn5
            }
            onClick={handleClickTab5}
          >
            {language === "UK" ? "Program" : "Programma"}
          </a>
        </div>
      </div>

      </div>

      <div className="uni-programs">
        <AIMLProgram disp={display1} />
        <CCProgram disp={display2} />
        <MBDProgram disp={display3} />
        <MITProgram disp={display4} />
        <MEProgram disp={display5} />
      </div>
    </div>
  );
}
