import React, { useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import TitleData from "../data/TitleData";

import Hobby from "./Hobby";
import HobbyList from "./HobbyList";
import HobbyArrow from "./HobbyArrow";
import HobbiesData from "../data/HobbiesData";

export default function Hobbies() {
  const language = useContext(LanguageContext)[0];

  function createHobby(Data) {
    return (
      <Hobby
        key={Data.id}
        active={Data.active}
        image={Data.image}
        // link={Data.link}
        // title={Data.title[language]}
        description={Data.description[language]}
      />
    );
  }

  return (
    <div className="hobbies-container container-fluid" id="hobbies">
      <div className="row">
        <div className="title-section-hobby title-section">{TitleData[language][5].toUpperCase()}</div>
      </div>

      <div
        id="carouselHobbies"
        className="carousel slide"
        data-ride="carousel"
        data-interval="3000"
        data-pause="hover"
      >
        <HobbyList />

        <div className="carousel-inner">{HobbiesData.map(createHobby)}</div>

        <HobbyArrow />
      </div>
    </div>
  );
}
