import React, { useState } from "react";
import profile1 from "../images/Other/profile1.png";
import profile2 from "../images/Other/profile2.png";

export default function Avatar() {
  const [isMousedOver, setMouseOver] = useState(false);

  function handleMouseOver() {
    setMouseOver(true);
  }

  function handleMouseOut() {
    setMouseOver(false);
  }
  return (
    <div>
      <a href="#avatar">
        <img
          className="profile-img frame"
          src={isMousedOver ? profile2 : profile1}
          onMouseOver={handleMouseOver}
          onMouseOut={handleMouseOut}
          alt="profile"
        />
      </a>
      <a href="#_" className="lightbox" id="avatar">
        <img src={profile1} alt="profile" style={{borderRadius:"100%"}}/>
      </a>
    </div>
  );
}
