import React, { useState, useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import WorkExperienceData from "../data/WorkExperienceData";
import MessageBtnData from "../data/MessageBtnData";

export default function Kinesso() {
  const language = useContext(LanguageContext)[0];
  const [visKinesso1, setVisKinesso1] = useState("none");
  const [visKinesso2, setVisKinesso2] = useState("");

  var [message, setMessage] = useState(0);

  function handleClickKinesso() {
    if (visKinesso1 === "none") {
      setVisKinesso1("");
      setVisKinesso2("none");
      setMessage(1);
    } else {
      setVisKinesso1("none");
      setVisKinesso2("");
      setMessage(0);
    }
  }

  return (
    <div className="timeline">
      <div className="timeline-content">
        <div className="timeline-icon">
          <img
            src={WorkExperienceData["kinesso"]["icon"]}
            alt="uni-img"
            className="company-icon centered"
          />
        </div>
        <h3 className="title">{WorkExperienceData["kinesso"]["title"]}</h3>
        <div className="description">
          <div className="row company-description">
            {WorkExperienceData["kinesso"]["name"]}&nbsp;
            <div className="tooltips tooltips-workexperience">
              <i className="fas fa-info-circle"></i>
              <span className="tooltiptext">
                {WorkExperienceData["kinesso"]["description"][language]}
              </span>
            </div>
          </div>
          <p>
            <div className="work-experience-date">
              <i class="fa fa-map-marker" aria-hidden="true"></i>&nbsp;
              {WorkExperienceData["kinesso"]["location"][language]}
            </div>
          </p>
          <p>
            <div className="work-experience-date">
              <i class="fa fa-calendar-o" aria-hidden="true"></i>&nbsp;
              {WorkExperienceData["kinesso"]["date"][language]}
            </div>
          </p>
          <ul style={{ display: visKinesso2 }} className="bullet-point">
            <li className="work-experience-experience">
              {WorkExperienceData["kinesso"]["experience"][language][0]}
            </li>
            <li className="work-experience-experience">{"..."}</li>
          </ul>
          <ul style={{ display: visKinesso1 }} className="bullet-point">
            <li className="work-experience-experience">
              {WorkExperienceData["kinesso"]["experience"][language][0]}
            </li>
            <li className="work-experience-experience">
              {WorkExperienceData["kinesso"]["experience"][language][1]}
            </li>
            <li className="work-experience-experience">
              {WorkExperienceData["kinesso"]["experience"][language][2]}
            </li>
            <li className="work-experience-experience">
              {WorkExperienceData["kinesso"]["experience"][language][3]}
            </li>
          </ul>
          <div style={{marginLeft : 48}}>
            <a
              className="btn shadow-none btn-outline-dark btn-sm btn-work-experience"
              onClick={handleClickKinesso}
              data-toggle="collapse"
              href="#kinesso"
              role="button"
            >
            {MessageBtnData[language][message]}
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
