import React, { useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import Kinesso from "./Kinesso";
import Adloox from "./Adloox";
import Adapt from "./Adapt";
import TitleData from "../data/TitleData";

export default function WorkExperiences() {
  const language = useContext(LanguageContext)[0];

  return (
    <div
      className="work-experience-container container-fluid"
      id="work-experience"
    >
      <div className="row">
        <div className="title-section">{TitleData[language][1].toUpperCase()}</div>
      </div>

      <div className="container">
        <div className="row">
          <div className="col-sm-12">
            <div className="main-timeline">
              <Kinesso />
              <Adloox />
              <Adapt />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
