import React, { useState, useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import WorkExperienceData from "../data/WorkExperienceData";
import MessageBtnData from "../data/MessageBtnData";

export default function Kinesso() {
  const language = useContext(LanguageContext)[0];
  const [visAdloox1, setVisAdloox1] = useState("none");
  const [visAdloox2, setVisAdloox2] = useState("");

  var [message, setMessage] = useState(0);

  function handleClickAdloox() {
    if (visAdloox1 === "none") {
      setVisAdloox1("");
      setVisAdloox2("none");
      setMessage(1);
    } else {
      setVisAdloox1("none");
      setVisAdloox2("");
      setMessage(0);
    }
  }

  return (
    <div className="timeline">
      <div className="timeline-content">
        <div className="timeline-icon">
          <img
            src={WorkExperienceData["adloox"]["icon"]}
            alt="uni-img"
            className="company-icon centered"
          />
        </div>
        <h3 className="title">{WorkExperienceData["adloox"]["title"]}</h3>
        <div className="description">
          <div className="row company-description">
            {WorkExperienceData["adloox"]["name"]}&nbsp;
            <div className="tooltips tooltips-workexperience">
              <i className="fas fa-info-circle"></i>
              <span className="tooltiptext">
                {WorkExperienceData["adloox"]["description"][language]}
              </span>
            </div>
          </div>
          <p>
            <div className="work-experience-date">
              <i class="fa fa-map-marker" aria-hidden="true"></i>&nbsp;
              {WorkExperienceData["adloox"]["location"][language]}
            </div>
          </p>
          <p>
            <div className="work-experience-date">
              <i class="fa fa-calendar-o" aria-hidden="true"></i>&nbsp;
              {WorkExperienceData["adloox"]["date"][language]}
            </div>
          </p>
          <ul style={{ display: visAdloox2 }} className="bullet-point">
            <li className="work-experience-experience">
              {WorkExperienceData["adloox"]["experience"][language][0]}
            </li>
            <li className="work-experience-experience">{"..."}</li>
          </ul>
          <ul style={{ display: visAdloox1 }} className="bullet-point">
            <li className="work-experience-experience">
              {WorkExperienceData["adloox"]["experience"][language][0]}
            </li>
            <li className="work-experience-experience">
              {WorkExperienceData["adloox"]["experience"][language][1]}
            </li>
            <li className="work-experience-experience">
              {WorkExperienceData["adloox"]["experience"][language][2]}
            </li>
            <li className="work-experience-experience">
              {WorkExperienceData["adloox"]["experience"][language][3]}
            </li>
          </ul>
          <div style={{marginLeft : 48}}>
            <a
              className="btn shadow-none btn-outline-dark btn-sm btn-work-experience"
              onClick={handleClickAdloox}
              data-toggle="collapse"
              href="#adloox"
              role="button"
            >
            {MessageBtnData[language][message]}
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
