import React from "react";

export default function HobbyList() {
  return (
    <ol className="carousel-indicators carousel-indicators-hobby">
      <li data-target="#carouselHobbies" data-slide-to="0"></li>
      <li data-target="#carouselHobbies" data-slide-to="1"></li>
      <li data-target="#carouselHobbies" data-slide-to="2"></li>
      <li data-target="#carouselHobbies" data-slide-to="3"></li>
    </ol>
  );
}
