import React, { useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import TitleData from "../data/TitleData";

import Project from "./Project";
import ProjectList from "./ProjectList";
import ProjectArrow from "./ProjectArrow";
import ProjectsData from "../data/ProjectsData";

export default function Projects() {
  const language = useContext(LanguageContext)[0];

  function createProject(Data) {
    return (
      <Project
        key={Data.id}
        active={Data.active}
        image={Data.image}
        link={Data.link}
        title={Data.title[language]}
        description={Data.description[language]}
      />
    );
  }

  return (
    <div className="projects-container container-fluid" id="projects">
      <div className="row">
        <div className="title-section">{TitleData[language][2].toUpperCase()}</div>
      </div>

      <div
        id="carouselProjects"
        className="carousel slide"
        data-ride="carousel"
        data-interval="3000"
        data-pause="hover"
      >
        <ProjectList />

        <div className="carousel-inner">{ProjectsData.map(createProject)}</div>

        <ProjectArrow />
      </div>
    </div>
  );
}
