import React from "react";

export default function HobbyArrow() {
  return (
    <div>
      <a
        className="carousel-control-prev"
        href="#carouselHobbies"
        role="button"
        data-slide="prev"
      >
        <span className="carousel-control-prev-icon carousel-control-prev-icon-hobby" aria-hidden="true"></span>
        <span className="sr-only">Previous</span>
      </a>
      <a
        className="carousel-control-next"
        href="#carouselHobbies"
        role="button"
        data-slide="next"
      >
        <span className="carousel-control-prev-icon carousel-control-next-icon-hobby" aria-hidden="true"></span>
        <span className="sr-only">Next</span>
      </a>
    </div>
  );
}
