import React, { useContext, useState } from "react";
import { LanguageContext } from "../context/LanguageContext";
import SummaryData from "../data/SummaryData";
import InfoData from "../data/InfoData";
import Presentation from "./Presentation";
import Info from "./Info";
import cv_uk from "../CV/Diego Bettega - CV (UK).pdf";
import cv_it from "../CV/Diego Bettega - CV (IT).pdf";
import cv_img from "../images/CV/CV.png";
import cv_img_hover from "../images/CV/CV_Hover.png";

export default function Summary() {
  const language = useContext(LanguageContext)[0];

  const [isMousedOver, setMouseOver] = useState(false);

  function handleMouseOver() {
    setMouseOver(true);
  }

  function handleMouseOut() {
    setMouseOver(false);
  }

  function createInfo(InfoData) {
    return (
      <Info
        key={InfoData.id}
        icon={InfoData.icon}
        text={InfoData.text[language]}
        href={InfoData.href}
        clickable={InfoData.clickable}
      />
    );
  }

  return (
    <div className="summary container-fluid" id="summary">
      <div className="row">
        <div className="cv-position col-sm-12">
          <a href={language === "UK" ? cv_uk : cv_it} download={language === "UK" ? "Diego Bettega - CV (UK)" : "Diego Bettega - CV (IT)"}>
            <img
              className="cv-img"
              src={isMousedOver ? cv_img_hover : cv_img}
              onMouseOver={handleMouseOver}
              onMouseOut={handleMouseOut}
              alt="CV-img"
            />
            <p>
              <a
                className={isMousedOver ? "btn shadow-none btn-outline-light btn-md cv-hover" : "btn shadow-none btn-outline-light btn-md btn-cv"}
                onMouseOver={handleMouseOver}
                onMouseOut={handleMouseOut}
              >
                {language === "UK" ? "Download my CV" : "Scarica il mio CV"}
              </a>
            </p>
          </a>
        </div>
      </div>
      <div className="row">
        <div className="col-sm-2 col-md-2 col-lg-3"></div>
        <Presentation />
        <div className="col-sm-6">{InfoData.map(createInfo)}</div>
      </div>
      <div className="row">
        <h1 className="summary-data">{SummaryData[language]}</h1>
      </div>
    </div>
  );
}
