import React, { useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import UniversityProgramData from "../data/UniversityProgramData";

export default function AIMLProgram(props) {

  const language = useContext(LanguageContext)[0];

  return (
    <div id="accordion" style={{ display: props.disp }}>
      <div className="card">
        <div className="card-header col-sm-12" id="headingOne">
          <h5 className="mb-0">
            <button
              className="btn btn-link"
              data-toggle="collapse"
              data-target="#collapseOne"
              aria-expanded="true"
              aria-controls="collapseOne"
            >
              {UniversityProgramData["MIT"]["titles"][0].toUpperCase()}
            </button>
          </h5>
        </div>

        <div
          id="collapseOne"
          className="collapse show"
          aria-labelledby="headingOne"
          data-parent="#accordion"
        >
          <div className="card-body">
          <ul className="bullet-point-alpha">
            <li>{UniversityProgramData["MIT"]["body1"][0]}</li>
            <li>{UniversityProgramData["MIT"]["body1"][1]}</li>
            <li>{UniversityProgramData["MIT"]["body1"][2]}</li>
            <li>{UniversityProgramData["MIT"]["body1"][3]}</li>
            <li>{UniversityProgramData["MIT"]["body1"][4]}</li>
            <li>{UniversityProgramData["MIT"]["body1"][5]}</li>
          </ul>
          </div>
        </div>
      </div>

    </div>
  );
}
