import React from "react";

export default function Hobby(props) {
  return (
    <div className={"carousel-item carousel-item-hobby" + props.active}>
      <div className="carousel-caption d-sm-block">
        <img className="picture-hobby" src={props.image} alt="hobby-pic" />

        <div className="btn-hobby">
          <a
            href={props.link}
            // className="btn btn-outline-light-hobby btn-sm"
            role="button"
            aria-pressed="true"
          >
            {props.title}
          </a>
        </div>
        <p className="description-hobby">{props.description}</p>
      </div>
    </div>
  );
}
