import React from "react";

export default function Trophy(props) {
  return (
    <div className="tooltips tooltips-education">
      <a href={"#" + props.reference}>
        <i className="fas fa-trophy"></i>
      </a>
      <a href="#_" className="lightbox" id={props.reference}>
        <img src={props.certificate} alt="" />
      </a>
      <span className="tooltiptext">{props.description}</span>
    </div>
  );
}
