import React, { useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import TitleData from "../data/TitleData";

import LightBoxCourses from "./LightBoxCourses";
import Course from "./Course";
import CoursesData from "../data/CoursesData";
import alteryx from "../images/Analytics/alteryx.png";
import alteryx_core from "../images/Certifications/alteryx_core.png";
import alteryx_advanced from "../images/Certifications/alteryx_advanced.png";

export default function Courses() {
  const language = useContext(LanguageContext)[0];

  function createCourse(Data) {
    return <Course key={Data.id} logo={Data.logo} title={Data.title} />;
  }

  return (
    <div className="courses-container container-fluid" id="courses">
      <div className="row">
        <div className="title-section">{TitleData[language][4].toUpperCase()}</div>
      </div>

      <div className="row course">
        <div className="course-img-div">
          <img src={alteryx} alt="course-img" className="course-img" />
        </div>
        <div className="course-title">
          <div className="row row-course-certification">
            alteryx Designer Core &ensp;
            <LightBoxCourses certificate={alteryx_core} reference="cert1" />
          </div>
        </div>
      </div>

      <div className="row course">
        <div className="course-img-div">
          <img src={alteryx} alt="course-img" className="course-img" />
        </div>
        <div className="course-title">
          <div className="row row-course-certification">
            alteryx Designer Advanced &ensp;
            <LightBoxCourses certificate={alteryx_advanced} reference="cert2" />
          </div>
        </div>
      </div>

      {CoursesData.map(createCourse)}
    </div>
  );
}
