import React from "react";

export default function LightBox(props) {
  return (
    <div>
      <a href={"#" + props.reference}>
        <i
          className="fas fa-award course-certification blinking"
          style={{ color: props.color }}
        ></i>
      </a>
      <a href="#_" className="lightbox" id={props.reference}>
        <img src={props.certificate} alt="" />
      </a>
    </div>
  );
}
